// RBC Icons
//
// Includes dozens of beautiful Visual Language-based RBC icons as a font  with
// more icons on the way!
//
// NOTE: Although the [Glyphicons](http://glyphicons.com/) Halfings font is also
// available for use, consider them deprecated, as they will likely be replaced
// once the RBC Icons set is completed.
//
// Weight: 1
//
// Markup: rbc-icons.html
//
// Styleguide components.rbc-icons

$rbc-icons-font-path: "#{$font-path}/rbc-icons";
$rbc-icons-font-family: "rbc-icons";


// Eliminate default button styling when combined with child icons
.btn.rbci-btn {
  background: transparent;
  border: 0;
  margin-left: 5px;
  padding: 0;
}

@font-face {
  font-family: $rbc-icons-font-family;
  src: url("#{$rbc-icons-font-path}/#{$rbc-icons-font-family}.eot");
  src:
    url("#{$rbc-icons-font-path}/#{$rbc-icons-font-family}.eot?#iefix") format("eot"),
    url("#{$rbc-icons-font-path}/#{$rbc-icons-font-family}.woff2") format("woff2"),
    url("#{$rbc-icons-font-path}/#{$rbc-icons-font-family}.woff") format("woff"),
    url("#{$rbc-icons-font-path}/#{$rbc-icons-font-family}.ttf") format("truetype"),
    url("#{$rbc-icons-font-path}/#{$rbc-icons-font-family}.svg#rbc-icons") format("svg");
}

@mixin rbci-styles {
  @include font-smoothing;

  font-family: $rbc-icons-font-family;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  padding: 1px;  // avoid cutting off edges of icons
  position: relative;
  // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
  text-decoration: none;
  text-transform: none;
  top: 0.15em;
}

@mixin icon-link($icon-name, $position: "after") {
  &::#{$position} {
    font-family: $rbc-icons-font-family;
    content: rbci-char($icon-name);
    display: inline-block;
    margin-left: 4px;
    vertical-align: middle;
  }
}

@import "generated";

.rbci {
  // for adding branding colors to icons
  @each $type, $color in $branding-types {
    &--#{$type}::before {
      color: $color;
    }
  }

  // TODO: add aliases
  // bars -> menu
  // angle-right -> link
}
