// Navbar
//
// Weight: 2
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: navbar.html
//
// Styleguide partials.navbar

$classname: ".rbc-navbar";

#{$classname} {
  @include font-size(14.5);

  display: flex;
  flex-direction: column;
  position: relative;

  &__row {
    width: 100%;
    padding: 0 5px;
  }

  &__row--primary {
    color: $white;
    background-color: $brand-primary;

    a {
      color: $white;
    }
  }

  &__row--secondary {
    background-color: map-get($rbc-colors, "gray-light-tint-3");

    a {
      color: $gray-darker;
    }
  }

  &__date {
    @include font-size(12);

    position: absolute;
    top: 1.25rem;
    right: 2rem;
  }
}

#{$classname}__list {
  list-style-type: none;
  padding: 0;
  margin: 0;

  &-item--active {
    border-bottom: 3px solid map-get($rbc-colors, "rbc-yellow");
  }

  .rbc-navbar__list-item--active a {
    margin-bottom: 0.8rem;
  }

  li {
    display: inline-block;

    a {
      display: block;
      margin: 1.1rem 1.5rem;
    }
  }
}
