// Transaction Filters
//
// Weight: 14
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: transaction-filters.html
//
// Styleguide partials.transaction-filters

$classname: ".rbc-transaction-filter";

#{$classname} {
  @include clearfix;
  background-color: map-get($rbc-colors, "gray-light-tint-2");
  padding: 17px 0 15px;

  &__form {
    float: left;
    margin-left: 15px;
  }

  &__search {
    float: right;
    padding: 12px 15px 0 0;

    a {
      color: map-get($rbc-colors, "rbc-blue");
    }
  }
}

#{$classname}__search-days {
  float: left;
  text-align: right;

  span {
    float: left;
    font-weight: 600;
    padding-right: 5px;
  }
}

#{$classname}__search-list {
  border-right: 1px solid $gray-light;
  display: inline-block;
  float: left;
  list-style: none;
  margin-right: 10px;
  padding: 0;

  li {
    display: inline-block;
    float: left;
    padding: 0 10px;

    a {
      color: map-get($rbc-colors, "rbc-blue");
      text-decoration: underline;
    }
  }
}
