// Ad Space
//
// Markup: widgets/ad-space/ad-space.html
//
// Weight: 2
//
// Styleguide widgets.ad-space

.rbc-ad-space-widget__section {
  @include media(992) {
    text-align: center;
  }

  a {
    display: inline-block;
    width: 100%;
    text-align: center;

    @include media(992) {
      display: inline;
    }

    img {
      max-width: 100%;
      margin: 10px auto 20px;
    }
  }
}
