// Transaction Table
//
// Weight: 14
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: transaction-table.html
//
// Styleguide partials.transaction-table

.rbc-transaction-table {
  &__header {
    @include clearfix;
  }

  &__info {
    float: left;

    h4 {
      @include font-size(18);
      display: inline-block;
      margin-bottom: 10px;
    }

    &__date {
      display: inline-block;
      padding: 0 0 0 4.5rem;
    }
  }

  &__actions {
    float: right;
    padding-top: 10px;
    text-align: right;
    text-decoration: underline;
  }

  a {
    color: map-get($rbc-colors, "rbc-blue");
  }

  // Right align the last three columns (headers and body)
  td:nth-of-type(2),
  td:nth-of-type(3),
  td:nth-of-type(4),
  th:nth-of-type(3),
  th:nth-of-type(4),
  th:nth-of-type(5) {
    text-align: right;
  }
}
