// Screen reader only helper
@mixin sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.rbc-sr-only {
  @include sr-only;
}

// Auto generate foreground color based on brightness of given background color
// @param {String} $bg-color - Background color
// @param {String} $light - Color to make foreground if background is dark
// @param {String} $dark - Color to make foreground if background is light
@function text-auto-color($bg-color, $light: $white, $dark: $black) {
  $color: $light;

  @if is-light($bg-color) {
    $color: $dark;
  }

  @return $color;
}

// Font smoothing mixin
// Only use for light text on dark background
// TODO: Remove after adding PostCSS
@mixin font-smoothing {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

// Font size mixin
@mixin font-size($sizevalue) {
  font-size: $sizevalue * 100% / 14.5;
}


/// Replace `$search` with `$replace` in `$string`
/// @author Hugo Giraudel
/// @param {String} $string - Initial string
/// @param {String} $search - Substring to replace
/// @param {String} $replace ('') - New value
/// @return {String} - Updated string
@function str-replace($string, $search, $replace: "") {
  $index: str-index($string, $search);

  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }

  @return $string;
}


// Media query mixins
//
@mixin media($width) {
  @media (max-width: #{$width}) {
    @content;
  }
}

@mixin media-xs() {
  @media (max-width: $screen-xs) {
    @content;
  }
}

@mixin media-sm() {
  @media (max-width: $screen-sm) {
    @content;
  }
}

@mixin media-md() {
  @media (max-width: $screen-md) {
    @content;
  }
}

@mixin media-lg() {
  @media (max-width: $screen-lg) {
    @content;
  }
}
