// Left Navigation
//
// Weight: 11
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: left-navigation.html
//
// Styleguide partials.left-navigation

$gutter-left: 20px;
$border-width-active: 4px;

.rbc-left-nav {
  padding-right: 0; // For increased gutter

  li {
    a {
      display: block;
      padding: 10px 5px 10px $gutter-left;
      margin-right: 10px;
    }

    > span {
      @include transition(all 150ms ease);
      display: block;
    }
  }

  &__skip {
    @include clearfix;
  }

  &__header {
    padding: 15px 0 15px $gutter-left;

    background-color: map-get($rbc-colors, "warm-grey-tint-2");

    > h3 {
      @include font-size(18);
      margin: 0;

      > a {
        color: #333;
      }
    }
  }

  &__list {
    padding: 0;
    list-style-type: none;

    > li {
      border-bottom: 1px solid map-get($rbc-colors, "gray-light-tint-3");

      &.rbc-left-nav__parent > a {
        padding: 15px 5px 15px $gutter-left;
      }

      a {
        color: map-get($rbc-colors, "rbc-blue");
      }

      .caret {
        transition: transform 150ms ease;
        float: right;
        position: relative;
        top: 8px;
      }
    }
  }

  &__secondary-list {
    padding: 0;
    list-style-type: none;

    > .rbc-left-nav__child--active {
      border-left: $border-width-active solid map-get($rbc-colors, "rbc-yellow");
      background-color: map-get($rbc-colors, "gray-light-tint-2");
      padding: 10px 10px 10px ($gutter-left - $border-width-active);
    }
  }

  &__parent {
    .caret {
      color: map-get($rbc-colors, "rbc-blue");
      transform: rotate(-90deg);
    }

    li:last-child {
      padding-bottom: 5px;
    }

    a[aria-expanded="true"] {
      color: #333;
      font-weight: bold;

      .caret {
        transform: rotate(0deg);
      }
    }
  }
}
