// Quick Pay
//
// Weight: 14
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: quick-pay.html
//
// Styleguide partials.quick-pay

.rbc-quick-payment {
  background-color: $brand-primary;
  padding: 12px 15px 15px;

  &__title {
    @include font-size(15);
    color: $white;
    font-weight: 400;
    line-height: 1.4;
    margin: 0;
    padding: 0 0 2px;
  }

  &__label {
    @include font-size(14.5);
    font-weight: 400;
    margin-bottom: 0;
  }

  fieldset {
    @include clearfix;
  }

  .input-group {
    float: left;
    padding-right: 10px;

    &--from {
      width: 32%;
    }

    &--to {
      width: 26%;
    }

    &--amount {
      width: 16%;
    }

    &--currency {
      width: 13%;
    }
  }

  .btn {
    width: 13%;
  }
}
