// Navs =======================================================================

.nav-pills {
  > li > a {
    border-radius: 0;
  }
}

.dropdown-menu {
  > li > a:hover,
  > li > a:focus {
    background-image: none;
  }
}


// Navbar =====================================================================

.navbar {
  &-inverse {
    .badge {
      background-color: $white;
      color: $brand-primary;
    }
  }
}
