// Links
//
// Markup: links.html
//
// Styleguide widgets.links


.rbc-links-widget__section {
  @include clearfix;

  .rbc-links-widget__list {
    list-style-type: none;
    margin: 0;
    padding-left: 0;
    float: left;
    width: 100%;

    @include media-md {
      width: 33.33%;

      &:first-of-type {
        padding-right: 1.3rem;
      }

      &:nth-of-type(2) {
        padding: 0 0.65rem;
        float: right;
      }

      &:last-of-type {
        padding-left: 1.3rem;
      }
    }

    @include media(680px) {
      width: 50%;

      &:nth-of-type(2) {
        padding-left: 1.3rem;
        padding-right: 0;
      }

      &:last-of-type {
        padding: 0;
      }
    }

    @include media-xs {
      width: 100%;

      &:first-of-type,
      &:nth-of-type(2) {
        padding: 0;
      }
    }

    li {
      padding-bottom: 15px;
    }

    a {
      color: map-get($rbc-colors, "rbc-blue");
    }
  }
}
