// Mad Lib
//
// Markup: mad-lib.html
//
// Weight: 20
//
// Styleguide components.mad-lib

.rbc-mad-lib {
  font-size: 20px;

  &__row {
    padding-top: 19px;
    margin-bottom: 30px;
  }

  select.form-control {
    background-size: 21px;

    &:focus {
      background-position: right 15px top 16px;
    }
  }

  .form-control {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    color: $brand-primary;
    display: inline-block;
    font-size: 20px;
    height: 43px;
    padding: floor($padding-base-vertical-focus / 2) 1px;
    width: 160px;

    &:focus {
      padding-top: floor($padding-base-vertical-focus / 2) - 2px;
      padding-bottom: floor($padding-base-vertical-focus / 2) - 3px;
    }

    &[type="number"] {
      width: 48px;
    }
  }

  label {
    height: 1px;
    margin: 0;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    width: 1px;
  }
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}
