// Typography =================================================================

// Typography
//
// The below provides typographical styles that are present across the site.
//
// Weight: 1
//
// Styleguide elements.typography


// Headings
//
// All HTML headings, <code>&lt;h1&gt;</code> through <code>&lt;h6&gt;</code>,
// are available. <code>.h1</code> through <code>.h6</code> classes are also
// available, for when you want to match the font styling of a heading but still
// want your text to be displayed inline.
//
// Markup: typography-headings.html
//
// Styleguide elements.typography.headings

// Headings with Subheadings
//
// Create lighter, secondary text in any heading with a generic
// <code>&lt;small&gt;</code> tag or the <code>.small</code> class.
//
// Markup: typography-headings-subheadings.html
//
// Styleguide: elements.typography.headings-subheadings

h2,
h3,
h4,
h5,
h6 {
  font-family: $roboto;
}


// Body Copy
//
// Weight: 2
//
// Styleguide elements.typography.body-copy

// Paragraph
//
// Markup: typography-paragraph.html
//
// Styleguide elements.typography.body-copy.paragraph

// Lead Body Copy
//
// Weight: 3
//
// Make a paragraph stand out by adding `.lead`.
//
// Experimental: Requires UX approval.
//
// Markup: typography-lead-body-copy.html
//
// Styleguide elements.typography.body-copy.lead


// Inline Text Elements
//
// Weight: 4
//
// Styleguide elements.typography.inline-text-elements

// Marked Text
//
// For highlighting a run of text due to its relevance in another context, use
// the `<mark>` tag.
//
// Experimental: Requires UX approval.
//
// Markup: typography-marked-text.html
//
// Styleguide elements.typography.inline-text-elements.marked-text

// Bold Text
//
// Weight: 5
//
// For emphasizing a snippet of text with a heavier font-weight.
//
// Markup: typography-bold-text.html
//
// Styleguide elements.typography.inline-text-elements.bold-text

// Italic Text
//
// For emphasizing a snippet of text with a heavier font-weight.
//
// Weight: 5
//
// Markup: typography-italic-text.html
//
// Styleguide elements.typography.inline-text-elements.italic-text

// Blockquotes
//
// For quoting blocks of content from another source within your document. Wrap
// `<blockquote>` around any HTML as the quote. For straight quotes, we
// recommend a `<p>`.
//
// Experimental: Requires UX approval.
//
// Weight: 6
//
// Markup: typography-blockquotes.html
//
// Styleguide elements.typography.inline-text-elements.blockquotes


// Lists
//
// Experimental: Requires UX approval.
//
// Weight: 6
//
// Styleguide elements.typography.lists

// Unordered
//
// A list of items in which the order does not explicitly matter.
//
// Markup: typography-unordered.html
//
// Styleguide elements.typography.lists.unordered

// Ordered
//
// A list of items in which the order does explicitly matter.
//
// Weight: 7
//
// Markup: typography-ordered.html
//
// Styleguide elements.typography.lists.ordered

// Unstyled
//
// Remove the default `list-style` and left margin on list items (immediate
// children only). <strong>This only applies to immediate children list
// items</strong>, meaning you will need to add the class for any nested lists
// as well.
//
// Weight: 7
//
// Markup: typography-unstyled.html
//
// Styleguide elements.typography.lists.unstyled

// Inline
//
// Place all list items on a single line with `display: inline-block;` and some
// light padding.
//
// Weight: 8
//
// Markup: typography-inline.html
//
// Styleguide elements.typography.lists.inline

// Description
//
// A list of terms with their associated descriptions.
//
// Weight: 9
//
// Markup: typography-description.html
//
// Styleguide elements.typography.lists.description

// Horizontal Description
//
// Make terms and descriptions in `<dl>` line up side-by-side. Starts off
// stacked like default `<dl>`s, but when the navbar expands, so do these.
//
// Markup: typography-horizontal-description.html
//
// Styleguide elements.typography.lists.description.horizontal

body {
  -webkit-font-smoothing: antialiased;
}

.text-primary,
.text-primary:hover {
  color: $brand-primary;
}

.text-success,
.text-success:hover {
  color: $brand-success;
}

.text-danger,
.text-danger:hover {
  color: $brand-danger;
}

.text-warning,
.text-warning:hover {
  color: $brand-warning;
}

.text-info,
.text-info:hover {
  color: $brand-info;
}

//== Localized font faces
//
//##

$fonts: (
  ("Roboto", "Roboto", (("Light", 300), ("Regular", 400), ("Bold", 700))),
  ("Fira Sans", "FiraSans", (("Light", 300), ("Regular", 400), ("Bold", 700)))
);

@each $font-name, $font-code, $styles in $fonts {
  @each $style-name, $style-val in $styles {
    $font-filename: str-replace($font-name, " ", "-");

    @if $style-name != "Regular" {
      $font-filename: "#{$font-filename}-#{$style-name}";
    }

    @include font-face(
      $font-name,
      "#{$font-path}/google/#{$font-name}/#{$font-filename}",
      $style-val,
      normal,
      $file-formats: eot woff2 woff ttf
    );
  }
}
