@mixin rbci($filename, $insert: before, $extend: true) {
  &:#{$insert} {
    @if $extend {
      // stylelint-disable at-rule-blacklist
      // autogenerated, and fine
      @extend %rbci;
    }

    @else {
      @include rbci-styles;
    }
    content: rbci-char($filename);
  }
}

