// Footer Columns
//
// Weight: 20
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: footer-columns.html
//
// Styleguide partials.footer-columns

.rbc-footer-columns {
  @include make-xs-column(6);
  @include make-sm-column(4);
  @include make-md-column(2.4);

  margin: 0;
  padding: 20px 15px 0;
  border-right: 1px solid map-get($rbc-colors, "gray-light-tint-2");

  &:last-child {
    border-right: 0;
  }

  @include media-md {
    padding: 20px 5% 0;

    &:nth-last-child(2),
    &:last-child {
      margin: 2rem 0 0;
    }

    &:nth-last-child(2) {
      clear: left;
    }

    &:nth-last-child(3) {
      border-right: 0;
    }
  }

  @include media-sm {
    padding: 20px 6% 0;

    &:nth-last-child(2) {
      clear: none;
      margin: 0;
    }

    &:nth-last-child(2),
    &:nth-last-child(4) {
      border-right: 0;
    }

    &:nth-last-child(3) {
      clear: left;
      border-right: 1px solid map-get($rbc-colors, "gray-light-tint-2");
    }

    &:last-child {
      margin: 0;
      border-right: 1px solid map-get($rbc-colors, "gray-light-tint-2");
    }
  }

  @include media-xs {
    width: 100%;

    &:first-child,
    &:nth-last-child(3),
    &:last-child {
      border: 0;
    }
  }

  .rbc-footer-columns__title {
    @include font-size(16);

    margin: 0 0 25px;
  }

  .rbc-footer-columns__list {
    list-style: none;
    padding: 0;
  }

  li {
    // Fix for focus
    line-height: 2;
    margin-bottom: 10px;
  }

  a {
    line-height: 1.2;
    color: map-get($rbc-colors, "rbc-blue");
  }

  i[class^="rbci-"] {
    display: inline;
    margin-left: 6px;
  }
}
