// Base
//
// Acts as a base for other widgets. Can accept any HTML.
//
// Styleguide widgets.base

// With Header
//
// Markup: with-header.html
//
// Styleguide widgets.base.with-header

// Without Header
//
// Markup: without-header.html
//
// Styleguide widgets.base.without-header

.rbc-widget {
  display: inline-block;
  width: 100%;
  padding: $widget-padding-base-vertical $widget-padding-base-horizontal ($widget-padding-base-vertical / 2);
  border: 1px solid map-get($rbc-colors, "gray-light-tint-3");
  margin-bottom: $line-height-computed;

  &__header {
    margin: 0 (-$widget-padding-base-horizontal) $widget-padding-base-vertical;
    border-bottom: 1px solid map-get($rbc-colors, "carbon-tint-3");
    background-color: map-get($rbc-colors, "warm-grey-tint-1");
  }

  &__title {
    @include font-size(18);

    margin: (-$widget-padding-base-vertical) 0 0;
    padding: 14px 20px;
    line-height: 1.4;
    color: $black;
  }
}
