// Buttons
//
// Weight: 6
//
// Styleguide elements.button


// Button tags
//
// Use the button classes on an <code>&lt;a&gt;</code>,
// <code>&lt;button&gt;</code>, or <code>&lt;input&gt;</code> element.
//
// NOTE: We do not recommend the use of styled `<a>` or `<input type="button">`
// tags in your code. Whenever possible, use `<button>`s instead.
//
// Markup: buttons-default.html
//
// Weight: -2
//
// Styleguide elements.button.default

.btn {
  transition: background-color 200ms ease, border 200ms ease;
  font-weight: $btn-font-weight;

  &:focus,
  &:active {
    box-shadow: none;
  }
}

// Options
//
// Use any of the available button classes to quickly create a styled button.
//
// Markup: buttons-options.html
//
// Experimental: The `success`, `warning`, `info`, and `danger` button colours use approved accent colours, but are not part of Refresh or Visual Language.
//
// Weight: -1
//
// Styleguide elements.button.options

.btn-default {
  &:hover {
    background-color: map-get($rbc-colors, "yellow-light-tint-2");
    border-color: map-get($rbc-colors, "yellow-light-tint-2");
  }

  &:active,
  &:focus {
    background-color: map-get($rbc-colors, "yellow-light-tint-3");
    border-color: map-get($rbc-colors, "yellow-light-tint-3");
  }
}

.btn-info {
  background-color: $white;
  border-color: $brand-primary;
  border-width: $border-width-focus;
  color: $brand-primary;
  padding: $padding-base-vertical-focus $padding-base-horizontal-focus;

  &:hover {
    background-color: $white;
    border-color: map-get($rbc-colors, "rbc-blue");
    color: $brand-primary;
  }

  &:active,
  &:focus {
    background-color: $white;
    border-color: map-get($rbc-colors, "blue-dark-tint-1");
    color: map-get($rbc-colors, "blue-dark-tint-1");
  }

  fieldset[disabled] &,
  &.disabled,
  &[disabled] {
    background: $white;
    border-color: $btn-link-disabled-color;
    color: map-get($rbc-colors, "gray-tint-2");
    opacity: 1;

    &:hover {
      background: $white;
      border-color: $btn-link-disabled-color;
    }
  }
}


// Active state
//
// Buttons will appear pressed (with a darker background and darker border) when
// active. For <code>&lt;button&gt;</code> elements, this is done via
// <code>:active</code>. For <code>&lt;a&gt;</code> elements, it's done with
// <code>.active</code>. However, you may use <code>.active</code> on
// <code>&lt;button&gt;</code>s (and include the
// <code>aria-pressed="true"</code> attribute) should you need to replicate the
// active state programmatically.
//
// Styleguide elements.button.activestate


// Disabled state
//
// Make buttons look unclickable by fading them to gray.
//
// Markup: buttons-disabled.html
//
// Styleguide elements.button.disabledstate

.btn-default,
.btn-primary,
.btn-success,
.btn-warning,
.btn-danger {
  fieldset[disabled] &,
  &.disabled,
  &[disabled] {
    background: $btn-link-disabled-color;
    border-color: $btn-link-disabled-color;
    color: map-get($rbc-colors, "gray-tint-2");
    opacity: 1;

    &:hover {
      background: $btn-link-disabled-color;
      border-color: $btn-link-disabled-color;
    }
  }
}

.btn-link {
  &.disabled,
  &[disabled] {
    color: map-get($rbc-colors, "gray-tint-2");
  }
}

// Sizes
//
// Fancy larger or smaller buttons? Add <code>.btn-lg</code>,
// <code>.btn-sm</code>, or <code>.btn-xs</code> for additional sizes.
//
// Experimental: Requires UX approval.
//
// Markup:
// <button type="button" class="btn btn-default {{modifier_class}}">Button</button>
// <button type="button" class="btn btn-primary {{modifier_class}}">Button</button>
//
// .btn-lg - Large
// .btn-sm - Small
// .btn-xs - Extra small
//
// Weight: 1
//
// Styleguide elements.button.sizes

.btn-lg {
  font-size: $font-size-base + 1;
  min-width: 160px;
}

.btn-sm {
  font-size: $font-size-base - 1;
}

.btn-xs {
  font-size: $font-size-base - 2;
}
