// Breadcrumbs
//
// Indicate the current page's location within a navigational hierarchy.
//
// Separators are automatically added in CSS through `:before` and `content`.
//
// Experimental: Requires UX approval.
//
// Markup: breadcrumbs.html
//
// Weight: 8
//
// Styleguide components.breadcrumbs

// Variables
//
//** Padding
$breadcrumb-padding-vertical:   8px;
$breadcrumb-padding-horizontal: 0;
//** Background color
$breadcrumb-bg:                 transparent;
//** Text color
$breadcrumb-color:              $gray-light;
//** Text color of current page in the breadcrumb
$breadcrumb-active-color:       $gray-light;
//** Textual separator for between breadcrumb elements
$breadcrumb-separator:          ">";


// Bootstrap component
//
@import "vendor/sass/bootstrap/breadcrumbs";


// Custom styles
//
.rbc-breadcrumb__list {
  // stylelint-disable at-rule-blacklist
  // extends Bootstrap component, eliminates rule duplication
  @extend .breadcrumb;

  border-bottom: 1px solid $gray-light;

  > li + li::before {
    content: $breadcrumb-separator;
  }
}
