// Forms
//
// For caveats and more information, visit the [Bootstrap forms
// documentation](http://getbootstrap.com/css/#forms).
//
// Weight: 6
//
// Styleguide elements.forms


// Basic example
//
// Individual form controls automatically receive some global styling. All
// textual `<input>`, `<textarea>`, and `<select>` elements with `.form-control`
// are set to `width: 100%;` by default. Wrap labels and controls in
// `.form-group` for optimum spacing.
//
// Markup: forms-basic.html
//
// Styleguide elements.forms.basic

.form-control {
  font-weight: 300;
  transition: none;

  &:focus {
    border-width: $border-width-focus;
    padding: $padding-base-vertical-focus $padding-base-horizontal-focus;
  }
}

label {
  margin-bottom: 8px;
  font-weight: 400;
}


// Basic dark background example
//
// Add `.form-inverse` to your form (which doesn't have to be a `<form>`) in
// order to add an alternate background.
//
// Markup: forms-basic-inverse.html
//
// Styleguide elements.forms.basic-dark-bg

.form-inverse {
  background: map-get($rbc-colors, "blue");
  padding: 12px 15px 15px;
  color: $white;

  p {
    color: $white;
  }

  a {
    color: $brand-default;
    text-decoration: underline;
  }

  .form-control:focus {
    border-color: $brand-default;
  }
}

// Inline form
//
// Add `.form-inline` to your form (which doesn't have to be a `<form>`) for
// left-aligned and inline-block controls. <strong>This only applies to forms
// within viewports that are at least 768px wide</strong>.
//
// Markup: forms-inline.html
//
// Styleguide elements.forms.inline


// Supported controls
//
// Examples of standard form controls supported in an example form layout.
//
// Styleguide elements.forms.supported-controls


// Inputs
//
// Most common form control, text-based input fields. Includes support for all
// HTML5 types: `text`, `password`, `datetime`, `datetime-local`, `date`,
// `month`, `time`, `week`, `number`, `email`, `url`, `search`, `tel`, and
// `color`.
//
// Inputs will only be fully styled if their `type` is properly declared.
//
// Markup: forms-inputs.html
//
// Styleguide elements.forms.supported-controls.inputs


// Textarea
//
// Form control which supports multiple lines of text. Change `rows` attribute as necessary.
//
// Markup: forms-textarea.html
//
// Styleguide elements.forms.supported-controls.textarea

textarea {
  resize: none;
}


// Checkboxes and radios
//
// Checkboxes are for selecting one or several options in a list, while radios
// are for selecting one option from many.
//
// Disabled checkboxes and radios are supported, but to provide a "not-allowed"
// cursor on hover of the parent `<label>`, you'll need to add the .disabled
// class to the parent `.radio`, `.radio-inline`, `.checkbox`, or
// `.checkbox-inline`.
//
// Weight: 2
//
// Markup: forms-checkboxes-radios.html
//
// Styleguide elements.forms.supported-controls.checkboxes-radios

.checkbox,
.radio {
  margin-top: 0;
  margin-bottom: 15px;

  input[type="checkbox"],
  input[type="radio"] {
    opacity: 0;
    width: auto;
    box-shadow: none;
    position: absolute;
  }

  label {
    cursor: default;
    position: relative;
    display: inline-block;
    padding-top: calc((#{$radio-size} - 1.5em) / 2);
    padding-bottom: calc((#{$radio-size} - 1.5em) / 2);
    padding-left: $radio-size + 20px;

    &:before {
      content: "";
      width: $radio-size;
      height: $radio-size;
      border: 1px solid $input-border;
      display: inline-block;
      background: $white;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
    }
  }
}

.radio label:before {
  border-radius: 50%;
}

.checkbox input[type="checkbox"] ~ label:before,
.radio input[type="radio"] ~ label:before {
  border: 1px solid $input-border;
}

.checkbox input[type="checkbox"]:checked ~ label:before,
.radio input[type="radio"]:checked ~ label:before {
  background: url("../img/elements/forms/check.svg") no-repeat center center $white;
  background-size: 20px 20px;
  padding: $padding-base-vertical-focus $padding-base-horizontal-focus;
}

.radio input[type="radio"]:checked ~ label:before {
  // TODO: Update to CSS instead of an SVG
  background: url("../img/elements/forms/bullet.svg") no-repeat center center $white;
}

input[type="checkbox"]:checked:focus ~ label:before,
input[type="radio"]:checked:focus ~ label:before {
  border: $border-width-focus solid $brand-primary;

  .form-inverse & {
    border: $border-width-focus solid $brand-default;
  }
}

// Selects
//
// Note that many native select menusnamely in Safari and Chromehave rounded
// corners that cannot be modified via `border-radius` properties.
//
// Weight: 2
//
// Markup: forms-selects.html
//
// Styleguide elements.forms.supported-controls.selects

select,
select.form-control {
  background-image: url("../img/elements/forms/chevron-down.svg");
  background-repeat: no-repeat;
  background-position: right 15px center;
  background-size: 15px;
  border-radius: 0;
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: "";
  -webkit-appearance: none;

  &:focus {
    background-position: right 14px center;
  }
}


// Selection Panels
//
// Weight: 3
//
// Markup: selection-panels.html
//
// Styleguide elements.forms.supported-controls.selection-panels

.rbc-label {
  border-bottom: 0;
  color: $gray-dark;
  display: inline-block;
  font-size: 100%;
  font-weight: 400;
  line-height: $line-height-base;
  max-width: 100%;
  margin-bottom: 8px;
  padding: 0;
  text-align: left;
}

$panel-margin: 10px;

.rbc-selection-panel {
  @include clearfix;

  margin-left: -1 * $panel-margin;
  margin-right: -1 * $panel-margin;

  @include media-md {
    margin-left: -1 * ($panel-margin / 2);
    margin-right: -1 * ($panel-margin / 2);
  }

  font-size: 0; // support display: inline-block in ie9

  &__panel {
    float: left;
    vertical-align: top;
    width: 25%;
    display: inline-block;

    form & {
      margin-bottom: 10px;
    }

    @include media-md {
      width: 50%;
    }

    > input[type=radio] {
      opacity: 0;
      width: auto;
      box-shadow: none;
      position: absolute;
    }

    > input[type=radio]:checked + label {
      border: 2px solid $brand-primary;
    }

    label {
      display: block;
      background: $white;
      position: relative;
      border: 2px solid map-get($rbc-colors, "gray-light-tint-3");
      box-shadow: 0;
      overflow: hidden;
      margin: -2px ($panel-margin - 2px);
      height: 200px;
      background-color: map-get($rbc-colors, "gray-light-tint-3");

      &:hover {
        border-color: $input-border;
        cursor: pointer;
      }

      @include media-md {
        margin: -2px (($panel-margin / 2) - 2px);
      }
    }
  }
}

// Slider
//
// Weight: 2
//
// Markup: slider.html
//
// Styleguide elements.forms.supported-controls.slider


// Step Slider
//
// Weight: 2
//
// Markup: step-slider.html
//
// Styleguide elements.forms.supported-controls.step-slider

.slider {
  @include clearfix();

  &--step {
    padding-bottom: 22px; // to allow for ruler
  }

  &__widget {
    display: inline-block;
    vertical-align: baseline;
    width: 100%;
    margin-right: 0;
    float: none;

    input[type="range"] {
      height: 1px;
      opacity: 0;
      overflow: hidden;
      position: absolute;
      width: 1px;
    }
  }
}

$slider-bar-size: 4px;
$slider-bar-margin: 17px;
$slider-handle-size: 35px;
$slider-label-margin: 28px;

.rangeslider {
  display: block;
  height: 50px;
  position: relative;
  width: 100%;

  &:before {
    content: "";
    display: block;
    position: absolute;
    left: 17px;
    right: 20px;
    background: $input-border;
    height: $slider-bar-size;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 10px;
  }

  &__fill {
    display: block;
    height: $slider-bar-size;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    &:before {
      content: "";
      display: block;
      position: absolute;
      left: $slider-bar-margin;
      right: (-1 * $slider-handle-size / 2);
      background: $brand-primary;
      height: 100%;
      border-radius: 10px;

      .rangeslider-step & {
        left: ($slider-bar-margin + 3px);
      }
    }
  }

  &--disabled {
    opacity: 0.4;
  }

  &__handle {
    background: $brand-primary;
    cursor: pointer;
    display: inline-block;
    width: $slider-handle-size;
    height: $slider-handle-size;
    border-radius: 50%;
    transform: translateZ(0);
    transition: transform 200ms ease;
    z-index: 1;
    position: absolute;
    top: 50%;
    margin-top: -($slider-handle-size / 2);
    touch-action: pan-y;
    -ms-touch-action: pan-y;

    &:active,
    &:focus,
    &.rangeslider__handle--interacted {
      transform: scale(1.4);
      transform-origin: center;
      box-shadow: 2px 0 2px rgba(0, 0, 0, 0.2);
    }
  }

  &-step__container {
    padding: 0 ($slider-handle-size / 2);
  }

  &__ruler {
    cursor: pointer;
    text-align: justify;
    color: $gray-dark;
    font-weight: 500;
    font-size: 18px;

    position: absolute;
    top: 0;
    right: ($slider-handle-size / 2);
    bottom: 0;
    left: ($slider-handle-size / 2);
    z-index: 1;
  }

  &__notch {
    display: inline-block;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-left: -20px;
    padding-top: 5px;

    &:before {
      position: absolute;
      content: "";
      display: block;
      width: 4px;
      height: 12px;
      background-color: $brand-primary;
      top: 50%;
      transform: translateY(-50%);
      margin-left: 16px; // magic
    }

    &-label {
      position: absolute;
      top: $slider-label-margin;
    }
  }
}

@include media($screen-xs - 1) {
  .slider__widget {
    width: 100%;
  }

  .slider__field {
    width: 100%;
    padding-top: 10px;

    .input {
      width: 88%;
      margin-left: 6%;
      margin-right: 6%;
    }
  }
}


// Disabled state
//
// Add the `disabled` boolean attribute on an input to prevent user
// interactions. Disabled inputs appear lighter and add a `not-allowed` cursor.
//
// Experimental: Requires UX approval.
//
// Weight: 3
//
// Markup: forms-disabled-state.html
//
// Styleguide elements.forms.disabled-state


// Disabled fieldsets
//
// Add the `disabled` attribute to a `<fieldset>` to disable all the controls
// within the `<fieldset>` at once.
//
// Weight: 3
//
// Markup: forms-disabled-fieldsets.html
//
// Styleguide elements.forms.disabled-state.disabled-fieldsets


// Readonly state
//
// Add the `readonly` boolean attribute on an input to prevent modification of
// the input's value. Read-only inputs appear lighter (just like disabled
// inputs), but retain the standard cursor.
//
// Weight: 3
//
// Markup: forms-readonly-state.html
//
// Styleguide elements.forms.readonly-state


// Help text
//
// Block level help text for form controls.
//
// Help text should be explicitly associated with the form control it relates to
// using the `aria-describedby` attribute. This will ensure that assistive
// technologies  such as screen readers  will announce this help text when the
// user focuses or enters the control.
//
// Weight: 4
//
// Markup: forms-help-text.html
//
// Styleguide elements.forms.help-text


// Validation states
//
// Bootstrap includes validation styles for error, warning, and success states
// on form controls. To use, add `.has-warning`, `.has-error`, or `.has-success`
// to the parent element. Any `.control-label`, `.form-control`, and
// `.help-block` within that element will receive the validation styles.
//
// Experimental: Requires UX approval.
//
// Weight: 4
//
// Markup: forms-validation-states.html
//
// Styleguide elements.forms.validation-states


// Optional icons
//
// You can also add optional feedback icons with the addition of `.has-feedback`
// and the right icon.
//
// <strong>Feedback icons only work with textual `<input class="form-control">`
// elements.</strong>
//
// Weight: 4
//
// Markup: forms-validation-optional-icons.html
//
// Styleguide elements.forms.validation-states.optional-icons


// With hidden .sr-only labels
//
// If you use the `.sr-only` class to hide a form control's `<label>` (rather
// than using other labelling options, such as the `aria-label` attribute),
// Bootstrap will automatically adjust the position of the icon once it's been
// added.
//
// Weight: 4
//
// Markup: forms-sr-only-labels.html
//
// Styleguide elements.forms.validation-states.optional-icons.hidden-sr-only-labels

.has-feedback {
  label:not(.sr-only) ~ i[class^="rbci-"] {
    top: 30px;
  }

  i[class^="rbci-"] {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2; // Ensure icon is above input groups
    display: block;
    width: $input-height-base;
    height: $input-height-base;
    line-height: $input-height-base;
    text-align: center;
    pointer-events: none;
  }
}

@mixin has-contextual-color($color) {
  i[class^="rbci-"],
  .help-block,
  .control-label,
  .radio,
  .checkbox,
  .radio-inline,
  .checkbox-inline,
  &.radio label,
  &.checkbox label,
  &.radio-inline label,
  &.checkbox-inline label,
  .form-control-feedback {
    color: $color;
  }

  .form-control,
  .input-group-addon {
    border: 1px solid $color;

    &:focus {
      border: $border-width-focus solid $color;
      padding: $padding-base-vertical-focus $padding-base-horizontal-focus;
    }
  }
}

.has-warning {
  @include has-contextual-color($brand-warning);
}

.has-error {
  @include has-contextual-color($brand-danger);

  // TODO: Add icon
  // .help-block {
  //
  // }
}

.has-success {
  @include has-contextual-color($brand-success);
}
