// Alerts
//
// A lightweight, flexible component that can optionally extend the entire viewport to showcase key content on your site.
//
// Experimental: Requires UX approval.
//
// Weight: 15
//
// Styleguide components.alert

$alert-types: (
  ("warning", $alert-warning-border, "warning"),
  ("info", $alert-info-border, "info"),
  ("success", $alert-success-border, "check-circle"),
  ("danger", $alert-danger-border, "warning")
);


// Examples
//
// Wrap any text and an optional dismiss button in `.alert` and one of the four
// contextual classes (e.g., `.alert-success`) for basic alert messages.
//
// Markup: alerts.html
//
// Styleguide components.alert.example

.alert {
  border-width: 5px 1px 1px;

  @each $type, $color-border, $icon in $alert-types {
    &-#{$type} {
      @include rbci($icon);

      &::before {
        color: $color-border;
        font-size: 1.4em;
        float: left;
        padding-right: 10px;
        position: relative;
        top: -2px;
      }
    }
  }
}


// Dismissible
//
// Build on any notice by adding an optional `.alert-dismissible` and close button.
//
// Markup: alerts-dismissable.html
//
// Weight: 1
//
// Styleguide components.alert.dismissable

// NOTE: allow for misspelling (Bootstrap convention)
.alert-dismissable .close,
.alert-dismissible .close {
  top: -2px;
}
