// Popovers ====================================================================

// Popovers
//
// Add small overlays of content, like those on the iPad, to any element for housing secondary information.
//
// Popovers whose both title and content are zero-length are never displayed.
//
// NOTE: Popovers require a series of settings, and have several exceptions
// and dependencies. Review [the Bootstrap
// documentation](https://getbootstrap.com/javascript/#popovers) for information
// on [usage](http://getbootstrap.com/javascript/#popovers-usage),
// [options](http://getbootstrap.com/javascript/#popovers-options),
// [methods](http://getbootstrap.com/javascript/#popovers-methods), and
// [events](http://getbootstrap.com/javascript/#popovers-events).
//
// NOTE #2: Popovers can only be triggered using clicks; `focus` and `hover` triggers from Bootstrap are not available in the framework.
//
// Experimental: Requires UX approval.
//
// Weight: 2
//
// Styleguide javascript.popovers

.popover {
  border: 1px solid $gray;
  color: $gray-darker;
  padding: 35px 25px 25px 20px;
  width: 300px;

  &.right > .arrow {
    border-right-color: $gray;
  }

  &.left > .arrow {
    border-left-color: $gray;
  }

  &.top > .arrow {
    border-top-color: $gray;
  }

  &.bottom > .arrow {
    border-bottom-color: $gray;
  }

  .popover-title {
    @include font-size(16);

    background: none;
    border: 0;
    color: $black;
    font-weight: 700;
    margin-top: 0;
    margin-left: initial;
    margin-bottom: 10px;
    padding: 0;
    white-space: pre-line;
  }

  .popover-content {
    padding: 0;
  }

  > .close {
    @include font-size(18);

    $color: map-get($rbc-colors, "rbc-blue");

    color: $color;
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
    line-height: 1;
    padding: 3px 3px 5px;
    border: $border-width-focus solid $white;
    border-radius: 100%;

    &:hover {
      color: darken($color, 10%);
    }

    &:focus,
    &:active {
      border-color: $color;
    }
  }
}


