// Legal
//
// Weight: 20
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: legal.html
//
// Styleguide partials.legal

.rbc-legal {
  margin: 0 20px 40px;
  padding-top: 22px;
  border-top: 1px solid map-get($rbc-colors, "gray-light-tint-3");

  p {
    @include font-size(12.5);

    color: $gray;
  }

  a {
    margin-left: 20px;
    color: map-get($rbc-colors, "rbc-blue");

    @include media-sm {
      display: block;
      margin: 0 0 1.5rem;
    }

    &:first-child {
      @include media-sm {
        margin-top: 1.5rem;
      }
    }
  }

  i[class^="rbci-"] {
    display: inline;
    margin-left: 6px;
  }
}
