// Need Help
//
// Markup: need-help.html
//
// Styleguide widgets.need-help

.rbc-help-widget {
  padding-bottom: 0;

  &__form {
    padding-bottom: $widget-padding-base-vertical;
  }

  &__faq {
    border-top: 1px solid map-get($rbc-colors, "gray-light-tint-3");
    margin: 0 (-$widget-padding-base-horizontal);

    a {
      color: map-get($rbc-colors, "rbc-blue");
    }
  }

  &__faq-button {
    display: block;
    padding: $widget-padding-base-vertical $widget-padding-base-horizontal;
    position: relative;
    width: 100%;

    i[class^="rbci-"] {
      position: absolute;
      right: $widget-padding-base-horizontal;
      top: 20px;
    }
  }

  &__faq-list {
    background-color: map-get($rbc-colors, "gray-light-tint-2");
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .05);
    list-style-type: none;
    margin: 0;
    padding: $widget-padding-base-vertical $widget-padding-base-horizontal;

    li {
      padding-bottom: 15px;

      &:last-of-type {
        padding-bottom: 0;
      }
    }
  }
}
