// Input Groups
//
// Weight: 17
//
// Extend form controls by adding text or buttons before, after, or on both sides of any text-based <code>&lt;input&gt;</code>. Use <code>.input-group</code> with an <code>.input-group-addon</code> or <code>.input-group-btn</code> to prepend or append elements to a single <code>.form-control</code>.
//
// Experimental: Requires UX approval.
//
// Styleguide components.input-groups


// Basic example
//
// Place one add-on or button on either side of an input. You may also place one on both sides of an input.
//
// <strong>We do not support multiple add-ons (<code>.input-group-addon</code> or <code>.input-group-btn</code>) on a single side.</strong>
//
// <strong>We do not support multiple form-controls in a single input group.</strong>
//
// Markup: input-groups-basic.html
//
// Styleguide components.input-groups.basicexample

// TODO: Fix input group growing 1px in height on focus of input
.input-group {
  border: 1px solid $input-border;

  input[type="text"].form-control {
    border: 1px solid transparent;
  }

  .input-group-addon {
    border: 1px solid transparent;

    &:first-child {
      border-right-color: $input-border;
    }

    &:last-child {
      border-left-color: $input-border;
    }
  }
}

.input-group-focus {
  $border-width: 1px;

  border-color: $brand-primary;
  border-width: $border-width-focus;

  input[type="text"].form-control {
    border-width: 0;
    height: $input-height-base - $border-width-focus;
    padding-left: $padding-base-horizontal;

    &:not(:first-child) {
      padding-left: $padding-base-horizontal + $border-width;
    }

    &:not(:last-child) {
      padding-right: $padding-base-horizontal + $border-width;
    }
  }

  .input-group-addon,
  .input-group-btn {
    &:first-child {
      border-right-color: $brand-primary;
      border-width: 0 $border-width 0 0;
    }

    &:last-child {
      border-left-color: $brand-primary;
      border-width: 0 0 0 $border-width;
    }
  }

  .input-group-btn {
    .btn {
      padding-bottom: $padding-base-vertical-focus;
      padding-top: $padding-base-vertical-focus;
    }

    &:first-child {
      .btn:first-of-type {
        padding-left: $padding-base-horizontal-focus;
      }
    }

    &:last-child {
      .btn {
        padding-left: $padding-base-horizontal + 1px;
        padding-right: $padding-base-horizontal-focus;
      }
    }
  }
}

@mixin input-group-contextual($color) {
  .input-group,
  .input-group-focus,
  .input-group-focus .input-group-addon {
    border-color: $color;
  }

  .input-group-addon,
  .input-group-btn {
    &:first-child {
      border-right-color: $color;
    }

    &:last-child {
      border-left-color: $color;
    }
  }
}

.has-success {
  @include input-group-contextual($brand-success);
}

.has-warning {
  @include input-group-contextual($brand-warning);
}

.has-error {
  @include input-group-contextual($brand-danger);
}


// Sizing
//
// Add the relative form sizing classes to the <code>.input-group</code> itself and contents within will automatically resizeno need for repeating the form control size classes on each element.
//
// Markup: input-groups-sizing.html
//
// Styleguide components.input-groups.sizing

.input-group-lg.input-group-focus {
  input[type="text"].form-control,
  .input-group-addon {
    padding: $padding-large-vertical $padding-large-horizontal;
    height: $input-height-large - 2px;
  }

  input[type="text"].form-control {
    padding-left: $padding-large-horizontal + 1px;
  }
}

.input-group-sm.input-group-focus {
  input[type="text"].form-control,
  .input-group-addon {
    padding: $padding-small-vertical $padding-small-horizontal;
    height: $input-height-small - 2px;
  }

  input[type="text"].form-control {
    padding-left: $padding-small-horizontal + 1px;
  }
}


