// Button addons
//
// Buttons in input groups are a bit different and require one extra level of nesting. Instead of <code>.input-group-addon</code>, you'll need to use <code>.input-group-btn</code> to wrap the buttons. This is required due to default browser styles that cannot be overridden.
//
// Weight: 3
//
// Markup: input-groups-button-addons.html
//
// Styleguide components.input-groups.buttonaddons

.input-group-btn:last-child {
  > .btn,
  > .btn-group {
    margin-right: -1px;
  }
}

