// Responsive classes
//
// Create responsive tables by wrapping any `.table` in `.table-responsive` to make them scroll horizontally on small devices (under 768px). When viewing on anything larger than 768px wide, you will not see any difference in these tables.
//
// Review [Bootstrap responsive tables section](http://getbootstrap.com/css/#tables-responsive) for caveats.
//
// Weight: 3
//
// Markup: tables-responsive-classes.html
//
// Styleguide elements.tables.responsive-classes

table,
.table {
  // stylelint-disable selector-max-compound-selectors
  // NOTE: RBC tables elements require long-tail styling rules
  border-top: 2px solid $black;

  a:not(.btn) {
    text-decoration: underline;
  }

  .dropdown-menu a {
    text-decoration: none;
  }

  // contextual colors
  @each $type, $color in $branding-types {
    .#{$type} {
      $bg-color: brand-bg-color($color);
      color: text-auto-color($bg-color);
    }
  }

  th {
    font-weight: 400;
  }

  tbody {
    tr td:last-of-type {
      padding-right: 10px;
    }

    tr th {
      padding-left: 10px;
    }
  }

  thead {
    tr:first-child th {
      color: $black;
      font-size: 12px;
      text-transform: uppercase;
    }

    tr th {
      border-bottom: 0;
      padding-top: 12px;
      padding-bottom: 10px;

      // scss-lint:disable NestingDepth
      &:first-of-type {
        padding-left: 10px;
      }

      // scss-lint:disable NestingDepth
      &:last-of-type {
        padding-right: 10px;
      }
    }
  }
}
