//## Monochromatic and brand colors for use across Bootstrap.

$white:                 map-get($rbc-colors, "white") !default;

$black:                 map-get($rbc-colors, "black") !default;
$black-alpha60:         rgba($black, 0.6) !default;

$gray-base:             $black !default;
$gray-dark:             map-get($rbc-colors, "gray-dark") !default;
$gray-darker:           map-get($rbc-colors, "gray-darker") !default;
$gray:                  map-get($rbc-colors, "gray") !default;
$gray-light:            map-get($rbc-colors, "gray-light-tint-4") !default;
$gray-lighter:          map-get($rbc-colors, "gray-light-tint-1") !default;
$gray-alpha60:          rgba(128, 128, 128, 0.25) !default;

$brand-default:         map-get($rbc-colors, "rbc-yellow") !default;
$brand-primary:         map-get($rbc-colors, "blue") !default;
$brand-success:         map-get($rbc-colors, "green") !default;
$brand-info:            map-get($rbc-colors, "blue-tint-1") !default;
$brand-warning:         map-get($rbc-colors, "orange") !default;
$brand-danger:          map-get($rbc-colors, "red") !default;

$transparent:           rgba($white, 0.1) !default;

$clr-product-pda: #6076ac ;


//== Branding
//
//##

$branding-types: (
  ("default", $brand-default),
  ("primary", $brand-primary),
  ("success", $brand-success),
  ("info", $brand-info),
  ("warning", $brand-warning),
  ("danger", $brand-danger)
);
