// Contextual Colours
//
// Convey meaning through color with a handful of emphasis utility classes. These may also be applied to links and will darken on hover just like our default link styles.
//
// Experimental: Requires UX approval.
//
// Markup:
// <p class="{{modifier_class}}">Primary - Nullam id dolor id nibh ultricies vehicula ut id elit.</p>
//
// .text-primary - Primary
// .text-success - Success
// .text-warning - Warning
// .text-danger - Danger
//
// Weight: -5
//
// Styleguide elements.helperclasses.contextualcolours


// Contextual Backgrounds
//
// Similar to the contextual text color classes, easily set the background of an element to any contextual class. Anchor components will darken on hover, just like the text classes.
//
// Experimental: Requires UX approval.
//
// Markup:
// <p class="{{modifier_class}}">Primary - Nullam id dolor id nibh ultricies vehicula ut id elit.</p>
//
// .bg-primary - Primary
// .bg-success - Success
// .bg-warning - Warning
// .bg-danger - Danger
//
// Weight: -4
//
// Styleguide elements.helperclasses.contextualbackgrounds


// Close Icon
//
// Use the generic close icon for dismissing content like modals and alerts.
//
// Markup: helper-classes-close-icon.html
//
// Weight: -3
//
// Styleguide elements.helperclasses.closeicon

.close {
  color: $text-color;
  text-decoration: none;
  text-shadow: none;
  opacity: 0.7;

  &:hover,
  &:focus {
    color: $text-color;
    opacity: 1;
  }
}


