  font-size: 18px;
  position: absolute;
  top: 0;
  right: 17.5px;
  bottom: 0;
  left: 17.5px;
  z-index: 1;
}

.rangeslider__notch {
  display: inline-block;
  text-align: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  margin-left: -20px;
  padding-top: 5px;
