// Action Bar
//
// Weight: 10
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: action-bar.html
//
// Styleguide partials.action-bar

.action-bar {
  font-family: Arial, sans-serif;

  &__alert-count {
    display: inline-block;
    margin-right: 4px;
    margin-left: 4px;
    text-decoration: none;

    span:hover {
      text-decoration: underline;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    -webkit-padding-start: 0;
    float: right;

    li {
      display: inline-block;
      margin-left: 5px;
    }

    a {
      color: map-get($rbc-colors, "rbc-blue");
      padding-top: 2px;
      line-height: 1;
    }
  }

  i[class^="rbci-"] {
    color: $gray-light;
  }
}
