// Account Toggle
//
// Weight: 10
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: account-toggle.html
//
// Styleguide partials.account-toggle

.account-toggle {
  &__button {
    @include font-size(18);

    display: inline-block;
    margin: 0;
    padding: 0;
    border: 0 none;
    color: map-get($rbc-colors, "rbc-blue");
    background: none;

    > .caret {
      margin-left: 4px;
    }
  }

  &__list {
    @include font-size(14.5);

    z-index: 1000;
    overflow-y: scroll;
    width: 380px;
    height: 315px;
    padding: 20px 20px 0;
    border: 1px solid $gray-light;
    box-shadow: 0 2px 5px 2px $gray-alpha60;
    background-color: $white;

    h3 {
      @include font-size(18);

      display: inline-block;
      margin: 0 0 5px;
      padding-bottom: 4px;
      padding-right: 20px;
      color: $black;
    }

    ul {
      list-style: none;
      margin-bottom: 30px;
      padding-left: 0;
    }

    li {
      padding-top: 10px;
      padding-bottom: 5px;
    }

    span {
      display: inline-block;
      margin-left: 5px;
      text-decoration: none;
      color: $gray;
    }
  }
}
