//
// Markup: account-overview.html
//
// Styleguide partials.account-overview

$classname: ".product";

#{$classname} {
  display: flex;
  padding: 12px 0 7px 15px;
  border: 1px solid map-get($rbc-colors, "gray-light-tint-3");
  background-color: map-get($rbc-colors, "gray-light-tint-2");

  &--pda {
    border-left: 3px solid $clr-product-pda;
  }

  a {
    color: map-get($rbc-colors, "rbc-blue");
    text-decoration: underline;
  }

  &__details {
    flex: 0 1 50%;
    padding: 0 20px 0 10px;
  }

  &__detail {
    display: flex;
    justify-content: space-between;

    &--bold {
      font-weight: bold;
    }

    &--separate {
      margin-top: 1.8rem;
    }

    p {
      margin: 0;
    }
  }

  &__img {
    img {
      width: 100px;
      height: auto;
    }
  }

  &__info {
    flex: 0 1 25%;
  }

  &__name {
    @include font-size(16);

    padding: 1rem 0 0;
    font-style: italic;
  }
}

#{$classname}__links {
  display: flex;
  flex: 0 1 35%;
  position: relative;
  border-left: 1px solid map-get($rbc-colors, "gray-light-tint-4");

  ul {
    list-style: none;
    margin: auto;
    padding: 0;
    text-align: center;

    li {
      display: block;
      margin: 0 0 2px;
    }
  }
}
