// RBC Banner
//
// Weight: 1
//
// Deprecated: This partial is part of the Refresh standard, and requires updates in order to conform to Visual Language standards.
//
// Markup: banner.html
//
// Styleguide partials.banner

.banner {
  display: flex;
  padding: 1.5rem;

  &__left,
  &__right {
    flex: 0 1 50%;
  }

  &__right {
    display: flex;
    justify-content: flex-end;
  }

  &__logo {
    img {
      width: 50px;
      height: auto;
    }
  }

  &__user-details {
    text-align: right;
  }

  &__user-actions {
    margin: 1rem 0 0;

    button {
      margin-left: 1rem;
    }
  }

  &__current-user {
    @include font-size(14);

    color: $gray-darker;
    text-transform: uppercase;

    &:hover {
      color: map-get($rbc-colors, "rbc-blue");
    }
  }

  &__site-map {
    @include font-size(14);

    color: map-get($rbc-colors, "rbc-blue");

    &:hover {
      color: map-get($rbc-colors, "rbc-blue");
    }
  }
}
